import axios from "axios";
import qs from "qs";
import { Toast } from "vant";
// import store from "@/store";

const DEFAULT_LOADING = true;

axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";
class KoreaRequest {
  instance = "";
  interceptors = {};
  showLoading = false;
  loading = "";

  constructor(config) {
    // axios
    this.instance = axios.create(config);
    // 
    this.interceptors = config.interceptors;
    this.showLoading = config.showLoading ?? DEFAULT_LOADING;
    // 
    this.instance.interceptors.request.use(
      this.interceptors?.requestInterceptors,
      this.interceptors?.requestInterceptorsCatch
    );
    // 
    this.instance.interceptors.response.use(
      this.interceptors?.responseInterceptors,
      this.interceptors?.requestInterceptorsCatch
    );
    //  
    this.instance.interceptors.request.use(
      (config) => {
        // loading
        if (this.showLoading) {
          // this.loading = "";
          this.loading = Toast.loading({
            message: "...",
            forbidClick: true,
            loadingType: "spinner",
            overlay: true,
          });
          console.log(this.loading, "loading");
        }
        return config;
      },
      (error) => {
        return error;
      }
    );
    this.instance.interceptors.response.use(
      (response) => {
        // this.loading?.close();
        console.log("", response);
        Toast.clear();
        // const { data } = response;
        // if (data.code === 2) {
        //   Dialog.alert({
        //     message: "",
        //   }).then(() => {
        //     store.dispatch("logoutAction").then(() => {
        //       window.location.reload();
        //     });
        //   });
        // }
        return response.data;
      },
      (error) => {
        // this.loading?.close();
        Toast.clear();
        return error;
      }
    );
  }

  /**
   * @description request 
   * @param config
   */
  request(config) {
    return new Promise((resolve, reject) => {
      // 
      if (config.interceptors?.requestInterceptors) {
        config = config.interceptors.requestInterceptors(config);
      }
      // loading
      if (config.showLoading === false) {
        this.showLoading = config.showLoading;
      }
      this.instance
        .request(config)
        .then((res) => {
          // 
          if (config.interceptors?.responseInterceptors) {
            res = config.interceptors.responseInterceptors(res);
          }
          this.showLoading = DEFAULT_LOADING;
          // 
          resolve(res);
        })
        .catch((err) => {
          this.showLoading = DEFAULT_LOADING;
          reject(err);
        });
    });
  }

  get(config) {
    return this.request({ ...config, method: "GET" });
  }

  post(config) {
    qs.stringify(config.data);
    return this.request({ ...config, method: "POST" });
  }

  delete(config) {
    return this.request({ ...config, method: "DELETE" });
  }

  patch(config) {
    return this.request({ ...config, method: "PATCH" });
  }

  put(config) {
    return this.request({ ...config, method: "PUT" });
  }

  options(config) {
    return this.request({ ...config, method: "OPTIONS" });
  }

  purge(config) {
    return this.request({ ...config, method: "PURGE" });
  }

  head(config) {
    return this.request({ ...config, method: "HEAD" });
  }

  link(config) {
    return this.request({ ...config, method: "LINK" });
  }

  unlink(config) {
    return this.request({ ...config, method: "UNLINK" });
  }
}

export default KoreaRequest;
