<template>
  <div class="blind-wrap">
    <div class="blind-img animate__animated animate__fadeIn">
      <img ref="blind" :src="showImg" alt="" />
    </div>
    <!---->
    <template v-if="status === 1">
      <div class="init-content">
        <div class="img-wrap">
          <div class="img img1" @click="changeStatus(2)">
            <img :src="initLeftImg" alt="" />
          </div>
          <div class="img img2" @click="changeStatus(3)">
            <img :src="initRightImg" alt="" />
          </div>
        </div>
      </div>
    </template>
    <template v-if="status === 2">
      <div class="three-one-wrap">
        <van-radio-group
          style="margin-left: 0"
          direction="horizontal"
          v-model="threeToOneSelect"
          class="animate__animated animate__fadeIn"
        >
          <template v-if="threeToOneNameList && threeToOneNameList.length > 0">
            <van-radio
              v-for="(item, index) in threeToOneNameList"
              :name="item"
              :key="index"
              checked-color="#D8986E"
              >{{ item.name }}</van-radio
            >
          </template>
        </van-radio-group>
      </div>
    </template>
    <template v-if="status === 3">
      <div class="six-one-wrap animate__animated animate__fadeIn">
        <div class="six-one-item">
          <template v-if="sixToOneImageList && sixToOneImageList.length > 0">
            <div
              class="item"
              v-for="(item, i) in sixToOneImageList"
              :key="i"
              :style="{ marginRight: i === 2 || i === 5 ? '0px' : '4px' }"
            >
              <img
                class="item-img"
                :src="item.url"
                referrerpolicy="no-referrer"
                :class="{ active_move: index == item.id }"
                alt=""
              />
            </div>
          </template>
        </div>
      </div>
    </template>
    <div
      class="btn-wrap"
      :style="{
        top: status === 1 ? '10px' : '8px',
      }"
    >
      <div class="btn" @click="changeStatus(2)"></div>
      <div class="btn" @click="changeStatus(3)"></div>
    </div>
    <div class="btn-link">
      <div class="btn-change" @click="getPrize">
        {{ status === 3 ? "" : "" }}
      </div>
    </div>
  </div>
</template>

<script>
import { errorInfo } from "@/utils";

export default {
  name: "blind-box-lottery",
  props: {
    listData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      initImg: require("@/assets/image/init-state.png"),
      initLeftImg: require("@/assets/image/init-left.png"),
      initRightImg: require("@/assets/image/init-right.png"),
      threeToOne: require("@/assets/image/three-to-one.png"),
      threeToOneSelect: "",
      sixToOne: require("@/assets/image/six-to-one.png"),
      showImg: null,
      status: 1, // 1. 2.  3. 
      activeClassName: "animate__animated animate__fadeIn delay-3s",
      active: false,

      //  
      timer: null,
      index: 1, // 0,
      count: 6, // 
      times: 0, // ,
      cycle: 6, // 
      speed: 100, // 
      prize: 1,
    };
  },
  computed: {
    threeToOneNameList() {
      return this.listData.filter((item) => item.type === 1);
    },
    sixToOneImageList() {
      return this.listData.filter((item) => item.type === 2);
    },
  },
  mounted() {
    // this.status = 1; // 
    this.changeImg();
  },
  methods: {
    changeImg() {
      if (this.status === 1) {
        this.showImg = this.initImg;
      } else if (this.status === 2) {
        this.showImg = this.threeToOne;
      } else {
        this.showImg = this.sixToOne;
      }
    },
    changeStatus(status) {
      if (this.status !== status) {
        this.status = status;
        this.threeToOneSelect = "";
        this.changeImg();
      }
    },
    openDialog() {
      this.$emit("openDialog");
    },
    getPrize() {
      switch (this.status) {
        case 1:
          this.status = 2;
          this.changeImg();
          break;
        case 2:
          this.getThreeToOne();
          break;
        case 3:
          this.getSixToOne();
          break;
      }
    },
    getThreeToOne() {
      if (this.threeToOneSelect === "") errorInfo("");
      else
        this.$emit("showDifferentDialog", {
          type: 1,
          ...this.threeToOneSelect,
        });
    },
    getSixToOne() {
      // this.startRoll();
      this.$emit("showDifferentDialog", {
        type: 5,
      });
    },
    //  
    // 
    startRoll() {
      this.times += 1; // 
      this.oneRoll(); // 
      //  && 
      if (this.times > this.cycle + 1) {
        clearTimeout(this.timer); // 
        this.times = 0; // 0
        let res = JSON.parse(localStorage.getItem("prizeSuccess"));
        this.index = res.data.id;
        if (res.code === 200) {
          this.$emit("showDifferentDialog", {
            type: 4,
            ...res.data,
          });
        } else if (res.code === 12) {
          this.$emit("showDifferentDialog", {
            type: 6,
            ...res.data,
          });
        }
      } else {
        if (this.times < this.cycle - 1) {
          this.speed -= 4; // 
        } else {
          this.speed += 10; // 
        }
        this.timer = setTimeout(this.startRoll, this.speed); //
      }
    },
    // 
    oneRoll() {
      let index = this.index; // 
      const count = 7; // 
      index += 1;
      if (index > count - 1) {
        index = 0;
      }
      this.index = index;
    },
  },
};
</script>

