import { request } from "@/request";

// 
export const getUserOpenId = (data) => {
  return request.post({
    url: "/api/doll/user/getOpenId",
    params: data,
  });
};

// 
export const getPageConfig = () => {
  return request.post({
    url: "/api/event/group/h5Config",
  });
};

// 
export const userLogin = (params) => {
  return request.post({
    url: `/api/doll/user/phone/login`,
    // params: params,
    data: params,
  });
};

